import React, { useState, useEffect } from "react";
import Sidebar from "../../components/Admin/Sidebar";
import TopBar from "../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import { Eye, Share2, FileText, File, Unlock, Users } from "lucide-react";
import { Modal, Button } from "antd";

import { FaDownload, FaEye } from "react-icons/fa"; // FontAwesome icons
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
function AdminUserAllDetails() {
  const [totalusers, settotalusers] = useState([]);
  const [totalCompany, settotalCompany] = useState([]);
  const [totalModule, settotalModule] = useState([]);
  const { id } = useParams();
  document.title = "Company Detail - Admin";
  const apiUrlAll = "https://blueprintcatalyst.com/api/admin/adminall/";
  const [istotalDocs, settotalDocs] = useState("");
  const [totalsignatory, settotalsignatory] = useState("");
  const [userData, setuserData] = useState("");
  const navigate = useNavigate();
  const [isinvestorReports, setinvestorReports] = useState("");
  const [LockedReports, setLockedReports] = useState("");
  const [UnlockedReports, setUnlockedReports] = useState("");
  const [issharedReport, setsharedReport] = useState("");
  const [totalinvestor, settotalinvestor] = useState("");
  const [total_shared_reports, settotal_shared_reports] = useState("");
  useEffect(() => {
    totalDocs();
  }, []);

  const totalDocs = async () => {
    let formData = {
      company_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "totalDocs", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      settotalDocs(respo.total_docs);
      settotalsignatory(respo.total_signatory);
      settotalinvestor(respo.total_investor);
      settotal_shared_reports(respo.total_shared_reports);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  //Investor Doc Latest Version
  const [records, setrecords] = useState([]);
  const [recordsPrev, setrecordsPrev] = useState([]);
  const [searchText, setSearchText] = useState("");
  function formatCurrentDate(input) {
    const date = new Date(input); // ✅ Convert input to Date

    if (isNaN(date)) return ""; // ⛔ Invalid date check

    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [selectedRole, setSelectedRole] = useState("");

  // Open modal with selected role
  const handleViewRole = (row) => {
    setSelectedRole(row.signature_role);
    setIsModalVisible(true);
  };

  // Close modal
  const handleCloseModal = () => {
    setIsModalVisible(false);
    setSelectedRole("");
  };
  const columns = [
    {
      name: "First Name",
      selector: (row) => row.first_name,
      sortable: true,
    },
    {
      name: "Last Name",
      selector: (row) => row.last_name,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.signatory_email,
      sortable: true,
    },
    {
      name: "Phone",
      selector: (row) => row.signatory_phone,
      sortable: true,
    },
    {
      name: "Role",
      selector: (row) => row.signatory_role, // used for sorting/search
      sortable: true,
      cell: (row) => (
        <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
          <span>{row.signatory_role}</span>
          <Button
            onClick={() => handleViewRole(row)}
            type="primary"
            icon={<FaEye />}
            size="small"
          >
            View
          </Button>
        </div>
      ),
    },

    {
      name: "Joined",
      selector: (row) => row.access_status,
      sortable: true,
      cell: (row) => {
        // Capitalize first letter
        const status = row.access_status
          ? row.access_status.charAt(0).toUpperCase() +
            row.access_status.slice(1)
          : "";

        // Determine color
        const color =
          row.access_status?.toLowerCase() === "pending"
            ? "red"
            : row.access_status?.toLowerCase() === "active"
            ? "green"
            : "black";

        return <span style={{ color }}>{status}</span>;
      },
    },
  ];
  const handleDownload = async (url) => {
    window.open(url, "_blank");
  };
  const filteredData = records.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  useEffect(() => {
    getcompanySignatory();
    investordocprevVersion();
    handleviewinvestordetailInthis();
  }, []);
  const getcompanySignatory = async () => {
    let formData = {
      company_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "getcompanySignatory",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;

      setrecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const investordocprevVersion = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "investordocprevVersion",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;

      setrecordsPrev(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columnsprev = [
    {
      name: "Report",
      selector: (row) => {
        if (row.type === "investor_update") {
          return "Investor Updates / Reporting";
        } else if (row.type === "dataroom") {
          return "Dataroom";
        } else {
          return row.report;
        }
      },
      sortable: true,
    },
    {
      name: "Date of Report",
      selector: (row) => formatCurrentDate(row.created_at),
      sortable: true,
    },
    {
      name: "Name of report",
      selector: (row) => row.document_name,
      sortable: true,
    },
    {
      name: "Download",
      cell: (row) => (
        <div className="d-flex gap-2">
          <button
            type="button"
            onClick={() => handleDownload(row.downloadUrl)}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-success fw-bold"
            title="Download"
          >
            <FaDownload />
          </button>
          <button
            type="button"
            onClick={() => handleviewinvestordetail(row.id)}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-primary"
            title="View Investor"
          >
            <Eye />
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const [recordsinvestor, setrecordsinvestor] = useState([]);
  const [recordsexport, setrecordsexport] = useState([]);
  const [viewInvestorDetail, setviewInvestorDetail] = useState(false);
  const [viewinvestordetailInthis, setviewinvestordetailInthis] = useState("");
  const handleviewinvestordetail = async (investor_updates_id) => {
    let formData = {
      id: investor_updates_id,
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "viewinvestorDetail", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setviewInvestorDetail(true);
      setrecordsinvestor(respo);
      setrecordsexport(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleviewinvestordetailInthis = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "viewinvestordetailInthis",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      setviewinvestordetailInthis(respo.length);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columnsinvestor = [
    {
      name: "Name of Report",
      selector: (row) => row.document_name,
      sortable: true,
    },
    {
      name: "Date of Report",
      selector: (row) => formatCurrentDate(row.date_view),
      sortable: true,
    },
    {
      name: "First Name",
      selector: (row) => row.first_name,
      sortable: true,
    },
    {
      name: "Last Name",
      selector: (row) => row.last_name,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.investor_email,
      sortable: true,
    },
    {
      name: "Mobile Number",
      selector: (row) => row.phone,
      sortable: true,
    },
    {
      name: "Ip Address",
      selector: (row) => row.ip_address,
      sortable: true,
    },

    {
      name: "Date Viewed",
      selector: (row) => formatCurrentDate(row.date_view),
      sortable: true,
    },
  ];
  const filteredDatainvestor = recordsinvestor.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  const filteredDataprev = recordsPrev.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  //Export File
  const [checkval, setcheckval] = useState(false);
  var apiURLINFile = "https://blueprintcatalyst.com/api/user/investorreport/";
  const handleCheckboxChange = (id) => {
    const updatedData = recordsinvestor.map((row) => {
      if (row.id === id) {
        return { ...row, shared: !row.shared };
      }
      return row;
    });

    setrecordsinvestor(updatedData);
    const anySelected = updatedData.some((row) => row.shared === true);
    setcheckval(anySelected);
  };
  const handleExcelFileDownload = async () => {
    const payload = {
      user_id: id,
      update_id: recordsinvestor.map((r) => r.investor_updates_id), // the investor_updates_id you want to export
    };
    const response = await axios.post(
      apiURLINFile + "exportInvestorExcel",
      payload,
      {
        responseType: "blob", // important for file download
      }
    );

    const url = window.URL.createObjectURL(new Blob([response.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", "investor_report.xlsx");
    document.body.appendChild(link);
    link.click();
  };

  const customStyles = {
    table: {
      style: {
        minWidth: "100%",
        boxShadow: "0 10px 15px rgba(0, 0, 0, 0.2)",
        border: "1px solid #00000036",
        borderRadius: "12px",
        overflow: "hidden",
        display: "block",
      },
    },
    headRow: {
      style: {
        display: "grid",
        gridTemplateColumns:
          "minmax(150px, 1fr) minmax(150px, 1fr) minmax(200px, 1fr) minmax(120px, 1fr) 80px 150px",
        backgroundColor: "#ff3f45",
        color: "#fff",
        fontWeight: "600",
        fontSize: "12px",
        textTransform: "uppercase",
        whiteSpace: "nowrap",
      },
    },
    headCells: {
      style: {
        padding: "8px",
        textAlign: "left",
      },
    },
    rows: {
      style: {
        display: "grid",
        gridTemplateColumns:
          "minmax(150px, 1fr) minmax(150px, 1fr) minmax(200px, 1fr) minmax(120px, 1fr) 80px 150px",
        fontSize: "14px",
        "&:hover": {
          backgroundColor: "#e8f0fe",
        },
      },
      stripedStyle: {
        backgroundColor: "#f4f6f8",
      },
    },
    cells: {
      style: {
        whiteSpace: "normal",
        overflow: "hidden",
        textOverflow: "ellipsis",
        padding: "8px",
        display: "flex",
        alignItems: "center",
      },
    },
    pagination: {
      style: {
        backgroundColor: "#fafafa",
        padding: "12px 16px",
      },
    },
  };
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 dashboard_padding w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <h2>{userData.company_name}</h2>
                  </div>
                  <div className="col-12">
                    <div className="row gy-4">
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Users />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Total Signatory</h4>
                            <p>{totalsignatory}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <File />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Total Docs</h4>
                            <p>{istotalDocs}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <FileText />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Total Investor</h4>
                            <p>{totalinvestor}</p>
                          </div>
                        </div>
                      </div>

                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Share2 />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Shared Reports</h4>
                            <p>{total_shared_reports}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="col-12">
                    <div className="card p-3">
                      <h5 className="mb-3">All Signatory</h5>
                      <div className="d-block">
                        <DataTable
                          columns={columns}
                          data={filteredData}
                          pagination
                          highlightOnHover
                          striped
                          responsive
                          customStyles={customStyles}
                          className="custom-scrollbar custome-icon"
                          noDataComponent={
                            <div className="text-center py-2">
                              <span>No users found</span>
                            </div>
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      {viewInvestorDetail && (
        <div
          className="modal fade show d-block"
          style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
          tabIndex="-1"
          role="dialog"
          aria-labelledby="paymentModalLabel"
          aria-hidden="false"
        >
          <div
            className="modal-dialog modal-dialog-centered modal-lg"
            style={{ maxWidth: "1366px" }}
          >
            <div className="modal-content rounded-4 shadow-lg p-4">
              <button
                type="button"
                className="btn-close position-absolute top-0 end-0 m-3"
                onClick={() => setviewInvestorDetail(false)}
                aria-label="Close"
              ></button>
              <h5>Investor Viewed</h5>

              <div className="mt-4">
                {/* Download Button aligned to right */}
                <div className="d-flex justify-content-end mb-2">
                  {/* <button
                    disabled={!checkval}
                    type="button"
                    onClick={() => handleExcelFileDownload()}
                    rel="noopener noreferrer"
                    className="btn btn-sm btn-outline-success fw-bold"
                    title="Download"
                  >
                    <FaDownload />
                  </button> */}
                </div>

                {/* DataTable */}
                <DataTable
                  columns={columnsinvestor}
                  data={filteredDatainvestor}
                  pagination
                  highlightOnHover
                  striped
                  responsive
                  customStyles={customStyles}
                  className="custom-scrollbar custome-icon"
                  noDataComponent={
                    <div className="text-center py-2">
                      <span>No users found</span>
                    </div>
                  }
                />
              </div>
            </div>
          </div>
        </div>
      )}
      <Modal
        title="Signatory Role"
        visible={isModalVisible}
        onOk={handleCloseModal}
        onCancel={handleCloseModal}
        okText="Close"
      >
        <p>{selectedRole || "No role assigned"}</p>
      </Modal>
    </>
  );
}

export default AdminUserAllDetails;
